<template>
	<div class="lottery">
		<div class="top">
			<p>积分</p>
			<button>规则></button>
		</div>
		<div class="bg1">
			<div class="circle">
				<div class="btn" @click="go"></div>
			</div>
		</div>	
		<div class="bottom-info">每次抽奖消耗20积分</div>
		
		<div class="awardRecords">
			
			<div class="title-bg">
				<!--<img src="" style="width: 100px;height: .8rem;"/>-->
				<!--<img src="../../../static/03.jpg" style=""/>-->
				获奖记录
				
			</div>
			<div style="height: .6rem;"></div>
			
			<div class="list"  v-for="item in items">
				<div>
					<div>{{item.info}}</div>
					<p>{{item.time}}</p>
				</div>
				<button>立即领取</button>
			</div>			
		</div>
		
		<div class="mask" v-show="is_mask"></div>
	    <div class="" v-show="is_cont">
	    	<div class="inner">
		    <section class="tip_text_container">           
	            <div>恭喜您 抽中了</div>
	            <p>20元优惠券</p>
	            <button class="s-btn">继续抽奖</button>
	        </section>
	        <div class="close"><img @click="closebox" src="../../assets/shopImage/delete.png"/></div>
	        </div>
	    </div>
	    
	</div>
</template>

<script>
    export default {
    	data(){
            return{
              is_mask:false, 
			  is_cont:false,
			  items:[
			  	{info:'获得50元优惠券一个',time:'2018/04/03 12:30'},
			  	{info:'获得30元优惠券一个',time:'2018/04/03 12:30'}
			  ]
            }
        },
        mounted(){
      
        },           
        methods: {        
			go(){
				this.is_mask=true
				this.is_cont=true				
			},
			closebox(){
				this.is_mask=false
				this.is_cont=false				
			}
        }
    }
</script>

<style scoped>
body{background: #FF7493;}
#app{background: #FF7493;}
.lottery{background: #FF7493;padding-bottom: .4rem;}
.top{display: flex;flex-direction: row;
justify-content: space-between;align-items: center;padding: .2rem;}
.top button{border: 0;outline: none;color: #fff;background: rgba(0,0,0,.2);border-radius: 1rem;padding:.1rem .2rem;}
.bg1{background: url(../../../static/imgs/ly-plate-c.gif)no-repeat;width: 6.6rem;height: 6.6rem;
background-size: 6.6rem 6.6rem;margin: auto;position: relative;}
.circle{background: url(../../../static/imgs/bg-lottery.png)no-repeat;width: 5.7rem;height: 5.7rem;
background-size: 5.7rem 5.7rem;margin: auto;position: absolute;top: .45rem;left: .45rem;}
.btn{background: url(../../../static/imgs/playbtn.png) no-repeat;width: 2.5rem;height: 2.5rem;
background-size: 2.5rem 2.5rem;position: absolute;top: 1.5rem;left: 1.6rem;}


.bottom-info{color: #fff;text-align: center;margin-top: .4rem;}

.awardRecords{margin: .2rem;margin-top: 1rem;background: #fff;padding: .4rem;border-radius: 4px;}
.title-bg{background: url(../../../static/03.jpg)no-repeat;width: 2rem;height: .8rem;line-height: .8rem;
background-size: 2rem .8rem;text-align: center;margin: auto;margin-top: -.8rem;/*color: #fff;*/}
.list{padding: .2rem;background: #f5f5f5;display: flex;flex-direction: row;
justify-content: space-between;align-items: center;margin-bottom: .2rem;border-radius: 4px;}
.list p{color: #999;}
.list button{border: 0;outline: none;background: #FF7493;padding: .2rem .4rem;color: #fff;border-radius: 1rem;}


.mask{position: fixed;top: 0%;left: 0%;background: rgba(0,0,0,0.6);width: 100%;height: 100%;z-index:0;}   
.alert_container{position: fixed;top: 0;left: 0;right: 0;bottom: 0;z-index: 1;}

.inner{position: fixed;z-index: 2;top: 50%;left: 50%; transform: translate(-50%,-50%);
text-align: center;border-radius: 4px;max-width:600px;margin:auto;width: 100%;margin: auto;} 
.inner p{margin: .3rem 0 .5rem;color: #FF7493;font-size: .3rem;}
.s-btn{
	border: 0;outline: none;background: #FF7493;padding: .2rem .4rem;color: #fff;border-radius: 1rem;font-size: .25rem;
}
.tip_text_container{border: .15rem #FF7493 solid;background:#fff;width:80%;padding: .6rem;margin: auto;border-radius: 8px;}
.test{border: .15rem #FF7493 solid;background:#fff;}
.close{
margin:auto;z-index: 3;margin-top: 1rem;margin-bottom: -2rem;} 
</style>


